.Header {
  background: red;
  background: -webkit-gradient(linear, left top, right bottom, from(#0400FF), to(#FA2146));
  background: -webkit-linear-gradient(top left, #0400FF, #FA2146);
  background: -o-linear-gradient(top left, #0400FF, #FA2146);
  background: linear-gradient(to bottom right, #0400FF, #FA2146);
  height: 234px;
}

.Header-textwrap {
    text-align: center;
}

.Header-title  {
    font-family: 'DejaVu Sans';
}

.Header-slug {
    padding-bottom: 1rem;
}

.Header-slug, .Header-title {
    color: white;
}


.Header-image {
    -webkit-animation:spin 4s linear infinite;
    animation:spin 4s linear infinite;
    height: auto;
}

@media screen and (min-width: 900px) {
    .Header-image {
        width: 100px;
    }
}

@media screen and (max-width: 900px) {
    .Header-image {
        width: 60px;
    }
}

@-webkit-keyframes spin {
    100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
    100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); }
}


